.wp-block-eedee-block-gutenslider {
  position: relative;
}
.wp-block-eedee-block-gutenslider .eedee-swiper-outer {
  position: relative;
  display: grid;
  grid-template-columns: minmax(0, 1fr);
  margin-top: var(--gutenslider-margin-y);
  margin-bottom: var(--gutenslider-margin-y);
  margin-left: var(--gutenslider-margin-x);
  margin-right: var(--gutenslider-margin-x);
}
.wp-block-eedee-block-gutenslider .eedee-background-div.no-media-background .eedee-background-div__overlay,
.wp-block-eedee-block-gutenslider .swiper-slide.bg-none .eedee-background-div__overlay,
.wp-block-eedee-block-gutenslider .swiper-slide.bg-gradient .eedee-background-div__overlay,
.wp-block-eedee-block-gutenslider .swiper-slide.bg-color .eedee-background-div__overlay {
  min-height: var(--gutenslider-min-height);
}
.wp-block-eedee-block-gutenslider .swiper {
  width: 100%;
  height: 100%;
  align-content: stretch;
}
.wp-block-eedee-block-gutenslider .swiper-wrapper {
  width: 100%;
  min-height: var(--gutenslider-min-height);
  overflow: visible;
}
.wp-block-eedee-block-gutenslider .swiper-zoom-container {
  height: 100%;
}
.wp-block-eedee-block-gutenslider .swiper-slide .swiper-zoom-container > div {
  width: 100%;
  height: 100%;
}
.wp-block-eedee-block-gutenslider .wp-block-eedee-block-gutenslide {
  display: grid;
}
.wp-block-eedee-block-gutenslider .wp-block-eedee-block-gutenslide .wp-block-eedee-block-gutenslide {
  display: none;
}
.wp-block-eedee-block-gutenslider .wp-block-eedee-block-gutenslide .eedee-background-div {
  height: 100%;
  /* todo is this needed ? */
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /* end todo is this needed ? */
  overflow-x: hidden;
  overflow-y: hidden;
  grid-area: 1/1;
  /* todo maybe also grid-area ? */
}
.wp-block-eedee-block-gutenslider .wp-block-eedee-block-gutenslide .eedee-background-div .eedee-background-div__overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.wp-block-eedee-block-gutenslider .wp-block-eedee-block-gutenslide .eedee-background-div video, .wp-block-eedee-block-gutenslider .wp-block-eedee-block-gutenslide .eedee-background-div img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: block;
  user-select: none;
}
.wp-block-eedee-block-gutenslider .wp-block-eedee-block-gutenslide .slide-content {
  display: flex;
  position: relative;
  overflow-x: hidden;
  flex-direction: column;
  grid-area: 1/1;
  overflow-wrap: break-word;
}
.wp-block-eedee-block-gutenslider .wp-block-eedee-block-gutenslide .slide-content > :first-child {
  margin-top: 0;
}
.wp-block-eedee-block-gutenslider .wp-block-eedee-block-gutenslide .slide-content > :last-child {
  margin-bottom: 0;
}
.wp-block-eedee-block-gutenslider .wp-block-eedee-block-gutenslide .slide-content > * {
  max-width: 100%;
}
.wp-block-eedee-block-gutenslider .wp-block-eedee-block-gutenslide .slide-link {
