}
.wp-block-eedee-block-gutenslider .wp-block-eedee-block-gutenslide.embed-background .eedee-background-div .wp-block-embed__wrapper {
  height: 100%;
  width: 100%;
}
.wp-block-eedee-block-gutenslider .wp-block-eedee-block-gutenslide.embed-background .eedee-background-div.wp-has-aspect-ratio.wp-embed-aspect-16-9 {
  width: calc(var(--gutenslider-min-height) * 1.7777777778);
}
