//gutenberg editor marker
#start-resizable-editor-section {
	display: none;
}

@media only screen and (min-width: 961px) {
	.wp-block-eedee-block-gutenslider.ed-desktop-hidden {
		display: none;
	}
}

// Medium devices (tablets, 600px to 992 px)
@media only screen and (min-width: 601px) and (max-width: 960px) {

	.wp-block-eedee-block-gutenslider {

		.eedee-swiper-outer {
			margin-top: var(--gutenslider-margin-y-md);
			margin-bottom: var(--gutenslider-margin-y-md);
			margin-left: var(--gutenslider-margin-x-md);
			margin-right: var(--gutenslider-margin-x-md);
		}

		.swiper-container.swiper-container-vertical {
			.swiper-wrapper {
				height: var(--gutenslider-min-height-md);
			}
		}

		.eedee-background-div.eedee-background-div.no-media-background,
		.eedee-background-div.no-media-background.eedee-background-div__overlay,
		.swiper-slide.bg-none.eedee-background-div__overlay,
		.swiper-slide.bg-gradient.eedee-background-div__overlay,
		.swiper-slide.bg-color.eedee-background-div__overlay,
		.swiper-wrapper,
		&.content-change .eedee-background-div {
			min-height: var(--gutenslider-min-height-md);
		}

		&.slides-auto {
			.eedee-swiper-outer,
			.wp-block-eedee-block-gutenslide,
			.swiper-wrapper,
			.swiper-wrapper > .swiper-slide,
			.swiper-container,
			.swiper-zoom-container
			.swiper-slide .swiper-zoom-container > div,
			.eedee-background-div,
			&.content-fixed .fixed-content-edit,
			.eedee-background-div img,
			.eedee-background-div video,
			.eedee-background-div .simpleParallax,
			.eedee.background-div.no-media-background .slide-content {
				height: var(--gutenslider-min-height-md);
				min-height: var(--gutenslider-min-height-md);
			}

			.eedee-background-div video {
				width: calc(var(--ed-vw) / var(--ed-vh) * var(--gutenslider-min-height-md));
			}
		}

		&.ed-tablet-hidden {
			display: none;
		}
	}

	.wp-block-eedee-block-gutenslider.content-fixed {
		.gutenslider-preview {
			min-height: var(--gutenslider-min-height-md);
		}

		.slide-content {
			min-height: var(--gutenslider-min-height-md);

			padding-top: var(--gutenslider-padding-y-md);
			padding-bottom: var(--gutenslider-padding-y-md);
			padding-left: var(--gutenslider-padding-x-md);
			padding-right: var(--gutenslider-padding-x-md);
		}
	}

	.wp-block-eedee-block-gutenslider.content-change {
		.slide-content {
			padding-top: var(--gutenslider-padding-y-md);
			padding-bottom: var(--gutenslider-padding-y-md);
			padding-left: var(--gutenslider-padding-x-md);
			padding-right: var(--gutenslider-padding-x-md);
		}
	}

	body:not(.blog):not(.archive) .wp-block-eedee-block-gutenslider.is-full {
		--gutenslider-min-height-md: calc(100vh - 2 * var(--gutenslider-margin-y-md)) !important;
		height: calc(100vh - 2 * var(--gutenslider-margin-y-md)) !important;

		.eedee-background-div img {
			height: calc(100vh - 2 * var(--gutenslider-margin-y-md)) !important;
		}
	}
}

